<template>
  <section class="bg-lime-lighten-3">
    <v-responsive class="pa-4">
      <v-alert
        v-if="error"
        class="mb-4"
        closable
        icon="mdi-cat"
        text="Whoops! Something went wrong"
        type="error"
      ></v-alert>
    </v-responsive>
    <main class="min-h-screen">
      <br />
      <br />

      <nav class="w-75 d-flex flex-column flex-md-row mx-auto">
        <v-text-field
          class="mr-0 mr-md-4"
          label="Will return a random cat saying :text"
          v-model="text"
        ></v-text-field>
        <v-select
          label="Filter"
          clearable
          :items="items"
          v-model="filter"
        ></v-select>
      </nav>

      <div class="ma-4 w-75 mx-auto">
        <!-- <p v-if="loading">Loading...</p> -->
        <v-img
          v-if="loading"
          class="bg-lime-lighten-4 rounded-lg"
          width="500"
          :aspect-ratio="1"
        ></v-img>
        <v-img
          v-else
          class="bg-lime-lighten-4 rounded-lg"
          width="500"
          :aspect-ratio="1"
          :src="cat"
          cover
          loading="lazy"
        ></v-img>
        <div class="align-self-center">
          <v-btn
            @click="generateCat"
            class="mt-4"
            :loading="loading"
            color="primary"
            >Give me a random Cat</v-btn
          >
        </div>
      </div>
      <br />
      <br />
    </main>
  </section>
</template>

<script>
export default {
  name: "App",

  components: {},

  data: () => ({
    cat: null,
    loading: false,
    text: "",
    items: ["blur", "mono", "sepia", "negative", "paint", "pixel"],
    filter: null,
    error: null,
  }),
  methods: {
    /**
     *
     * * Get a random cat.
     *
     */
    generateCat: function () {
      this.loading = true;
      if (this.text !== "") {
        fetch("https://cataas.com/cat/says/" + this.text)
          .then((response) => {
            if (!response.ok) {
              throw new Error(
                `The response is "${response.statusText}" and the HTTP Status is "${response.status}".`
              );
            }
            return response.url;
          })
          .then((catURL) => {
            this.cat = catURL;

            this.loading = false;
          })
          .catch((error) => {
            this.error = error;

            this.loading = false;
          });
      } else if (this.filter !== null) {
        fetch("https://cataas.com/cat?filter=" + this.filter)
          .then((response) => {
            if (!response.ok) {
              throw new Error(
                `The response is "${response.statusText}" and the HTTP Status is "${response.status}".`
              );
            }
            return response.url;
          })
          .then((catURL) => {
            this.cat = catURL;

            this.loading = false;
          })
          .catch((error) => {
            this.error = error;

            this.loading = false;
          });
      } else {
        fetch("https://cataas.com/cat")
          .then((response) => {
            if (!response.ok) {
              throw new Error(
                `The response is "${response.statusText}" and the HTTP Status is "${response.status}".`
              );
            }
            return response.url;
          })
          .then((catURL) => {
            this.cat = catURL;

            this.loading = false;
          })
          .catch((error) => {
            this.error = error;

            this.loading = false;
          });
      }
    },
  },
};
</script>
